  const galleryContainer = document.querySelector(".gallery");
  const boxes = [];
  const galleryItems = images.map((image) => {
    
    const galleryItem = document.createElement("li");
    galleryItem.classList.add("gallery-item");
  
    const galleryLink = document.createElement("a");
    galleryLink.classList.add("gallery-link");
    galleryLink.href = image.original;
  
    const galleryImage = document.createElement("img");
    galleryImage.classList.add("gallery-image");
    galleryImage.src = image.preview;
    galleryImage.source = image.original;
    galleryImage.alt = image.description;
    galleryImage.width = 360;
    galleryImage.height = 200;

    galleryLink.appendChild(galleryImage);
    galleryItem.appendChild(galleryLink);
    boxes.push(galleryItem);
  
  });
  
  galleryContainer.append(...boxes);
  
  
  document.addEventListener('DOMContentLoaded', function(){
    const gallery = new SimpleLightbox('.gallery-item a');
    gallery.on('show.simplelightbox', function(){
        gallery.options.captionDelay = 250;
        gallery.options.captionPosition = 'bottom';
        gallery.options.captionType = 'attr';
        gallery.options.captionClass = 'gallery-caption';
        gallery.options.captionSelector = 'img';
        gallery.options.captionsData = 'alt';

    });
  });

  